"use client";

import React, { createContext, useContext, useReducer, useEffect, type ReactNode } from 'react';
import type { Product } from './types';
import { useToast } from '@/hooks/use-toast';
import { useSession } from 'next-auth/react';

type WishlistState = {
    items: Product[];
};

type WishlistAction =
    | { type: 'ADD_ITEM'; payload: Product }
    | { type: 'REMOVE_ITEM'; payload: { id: string } }
    | { type: 'SET_STATE'; payload: WishlistState };

const WishlistContext = createContext<{
    items: Product[];
    addToWishlist: (product: Product) => void;
    removeFromWishlist: (id: string) => void;
    toggleWishlist: (product: Product) => void;
    isInWishlist: (id: string) => boolean;
    wishlistCount: number;
    isLoading: boolean;
} | undefined>(undefined);

function wishlistReducer(state: WishlistState, action: WishlistAction): WishlistState {
    switch (action.type) {
        case 'ADD_ITEM': {
            if (state.items.find(item => item.id === action.payload.id)) {
                return state;
            }
            return {
                ...state,
                items: [...state.items, action.payload],
            };
        }
        case 'REMOVE_ITEM':
            return {
                ...state,
                items: state.items.filter(item => item.id !== action.payload.id),
            };
        case 'SET_STATE':
            return action.payload;
        default:
            return state;
    }
}

const initialState: WishlistState = {
    items: [],
};

export function WishlistProvider({ children }: { children: ReactNode }) {
    const { toast } = useToast();
    const { data: session, status } = useSession();
    const [state, dispatch] = useReducer(wishlistReducer, initialState);
    const [isLoading, setIsLoading] = React.useState(true);

    // Initial load from localStorage (for guest users or pre-login)
    useEffect(() => {
        try {
            const storedWishlist = localStorage.getItem('cuddle_wishlist');
            if (storedWishlist) {
                dispatch({ type: 'SET_STATE', payload: JSON.parse(storedWishlist) });
            }
        } catch (error) {
            console.error("Could not load wishlist from local storage", error);
        } finally {
            if (status !== 'authenticated') {
                setIsLoading(false);
            }
        }
    }, [status]);

    // Load from MongoDB if authenticated
    useEffect(() => {
        const fetchWishlist = async () => {
            if (status === 'authenticated') {
                try {
                    const response = await fetch('/api/user/wishlist');
                    if (response.ok) {
                        const data = await response.json();
                        dispatch({ type: 'SET_STATE', payload: { items: data.items } });
                    }
                } catch (error) {
                    console.error("Failed to fetch wishlist from DB", error);
                } finally {
                    setIsLoading(false);
                }
            }
        };

        if (status === 'authenticated') {
            fetchWishlist();
        }
    }, [status]);

    // Save to localStorage
    useEffect(() => {
        try {
            if (state.items.length > 0 || localStorage.getItem('cuddle_wishlist')) {
                localStorage.setItem('cuddle_wishlist', JSON.stringify(state));
            }
        } catch (error) {
            console.error("Could not save wishlist to local storage", error);
        }
    }, [state]);

    const addToWishlist = async (product: Product) => {
        dispatch({ type: 'ADD_ITEM', payload: product });

        if (status === 'authenticated') {
            try {
                await fetch('/api/user/wishlist', {
                    method: 'POST',
                    headers: { 'Content-Type': 'application/json' },
                    body: JSON.stringify({ productId: product.id, action: 'add' }),
                });
            } catch (error) {
                console.error("Failed to sync wishlist add to DB", error);
            }
        }

        toast({
            title: "Added to Wishlist!",
            description: `${product.name} is now in your wishlist.`,
        });
    };

    const removeFromWishlist = async (id: string) => {
        const item = state.items.find(item => item.id === id);
        if (item) {
            dispatch({ type: 'REMOVE_ITEM', payload: { id } });

            if (status === 'authenticated') {
                try {
                    await fetch('/api/user/wishlist', {
                        method: 'POST',
                        headers: { 'Content-Type': 'application/json' },
                        body: JSON.stringify({ productId: id, action: 'remove' }),
                    });
                } catch (error) {
                    console.error("Failed to sync wishlist remove from DB", error);
                }
            }

            toast({
                title: "Removed from Wishlist",
                description: `${item.name} has been removed from your wishlist.`,
            });
        }
    };

    const toggleWishlist = (product: Product) => {
        if (isInWishlist(product.id)) {
            removeFromWishlist(product.id);
        } else {
            addToWishlist(product);
        }
    };

    const isInWishlist = (id: string) => {
        return state.items.some(item => item.id === id);
    };

    const wishlistCount = state.items.length;

    return (
        <WishlistContext.Provider value={{
            items: state.items,
            addToWishlist,
            removeFromWishlist,
            toggleWishlist,
            isInWishlist,
            wishlistCount,
            isLoading
        }}>
            {children}
        </WishlistContext.Provider>
    );
}

export function useWishlist() {
    const context = useContext(WishlistContext);
    if (context === undefined) {
        throw new Error('useWishlist must be used within a WishlistProvider');
    }
    return context;
}
