"use client"

import { useState } from "react"
import { useRouter } from "next/navigation"
import { signIn } from "next-auth/react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { registerUser } from "@/actions/auth-actions"
import { Loader2, AlertCircle } from "lucide-react"
import { Alert, AlertDescription } from "@/components/ui/alert"

interface LoginFormProps {
    onSuccess?: () => void;
}

export function LoginForm({ onSuccess }: LoginFormProps) {
    const router = useRouter()
    const [isLogin, setIsLogin] = useState(true)
    const [loading, setLoading] = useState(false)
    const [error, setError] = useState("")

    const handleSubmit = async (e: React.FormEvent<HTMLFormElement>) => {
        e.preventDefault()
        setLoading(true)
        setError("")

        const formData = new FormData(e.currentTarget)
        const email = formData.get("email") as string
        const password = formData.get("password") as string

        try {
            if (isLogin) {
                const res = await signIn("credentials", {
                    email,
                    password,
                    redirect: false,
                })

                if (res?.error) {
                    setError("Invalid email or password")
                } else {
                    if (onSuccess) {
                        onSuccess();
                    }
                    // Hard reload to update session state and fix "freezing" issue
                    window.location.reload()
                }
            } else {
                const res = await registerUser(formData)
                if (res.success) {
                    setIsLogin(true)
                    setError("Registration successful! Please login.")
                    // Optional: could auto-fill email here
                } else {
                    setError(res.message || "Registration failed")
                }
            }
        } catch (err) {
            console.error(err)
            setError("An error occurred. Please try again.")
        } finally {
            setLoading(false)
        }
    }

    return (
        <Card className="border-0 shadow-none w-full max-w-sm mx-auto">
            <CardHeader className="px-0">
                <CardTitle className="text-2xl">{isLogin ? "Login" : "Register"}</CardTitle>
                <CardDescription>
                    {isLogin
                        ? "Enter your email below to login to your account"
                        : "Create an account to get started"}
                </CardDescription>
            </CardHeader>
            <CardContent className="px-0">
                {error && (
                    <Alert variant={error.includes("successful") ? "default" : "destructive"} className="mb-4">
                        <AlertCircle className="h-4 w-4" />
                        <AlertDescription>{error}</AlertDescription>
                    </Alert>
                )}

                <form onSubmit={handleSubmit} className="flex flex-col gap-4">
                    {!isLogin && (
                        <div className="grid gap-2">
                            <Label htmlFor="name">Full Name</Label>
                            <Input
                                id="name"
                                name="name"
                                placeholder="John Doe"
                                required={!isLogin}
                                disabled={loading}
                            />
                        </div>
                    )}

                    <div className="grid gap-2">
                        <Label htmlFor="email">Email</Label>
                        <Input
                            id="email"
                            name="email"
                            type="email"
                            placeholder="m@example.com"
                            required
                            disabled={loading}
                        />
                    </div>

                    <div className="grid gap-2">
                        <div className="flex items-center">
                            <Label htmlFor="password">Password</Label>
                        </div>
                        <Input
                            id="password"
                            name="password"
                            type="password"
                            required
                            disabled={loading}
                        />
                    </div>

                    <Button type="submit" className="w-full" disabled={loading}>
                        {loading && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                        {isLogin ? "Login" : "Register"}
                    </Button>
                </form>

                <div className="mt-4 text-center text-sm">
                    <button
                        type="button"
                        onClick={() => {
                            setIsLogin(!isLogin)
                            setError("")
                        }}
                        className="underline underline-offset-4 hover:text-primary"
                    >
                        {isLogin
                            ? "Don't have an account? Sign up"
                            : "Already have an account? Sign in"}
                    </button>
                </div>
            </CardContent>
        </Card>
    )
}
